.header {
  font-size   : 14px;
  font-weight : normal;
  font-style  : normal;

  // .header__container
  &__container {
    display         : flex;
    align-items     : center;
    justify-content : space-between;

    // .header__container--top
    &--top {
      padding-top    : 10px;
      padding-bottom : 10px;
    }

    // .header__container--middle
    &--middle {
      position       : relative;
      padding-top    : 15px;
      padding-bottom : 15px;

      @media screen and (max-width : 1139px) {
        padding-top    : 21px;
        padding-bottom : 21px;
      }
      @media screen and (max-width : 560px) {
        align-items    : flex-start;
        padding-top    : 14px;
        padding-bottom : 9px;
      }
    }

    // .header__container--bottom
    &--bottom {
      padding-top    : 19px;
      padding-bottom : 19px;
    }
  }

  // .header__row
  &__row {
    // .header__row--top
    &--top {
      background-color : #f8f8f8;

      @media screen and (max-width : 1139px) {
        display : none;
      }
    }

    // .header__row--middle
    &--middle {
      position : relative;

      @media screen and (max-width : 1139px) {
        margin-bottom : 56px;
      }
    }

    // .header__row--bottom
    &--bottom {
      background-color : #00cc52;

      @media screen and (max-width : 1139px) {
        display : none;
      }

    }
  }

  // .header__main-menu-nav
  &__main-menu-nav {

  }

  // .header__call-order
  &__call-order {
  }

  // .header__logo
  &__logo {
  }

  // .header__search
  &__search {
    position : relative;
    flex     : 0 1 358px;

    @media screen and (max-width : 1139px) {
      position         : absolute;
      right            : 0;
      bottom           : -56px;
      left             : 0;
      flex             : 1 1 100%;
      padding          : 8px 40px;
      background-color : #00cc52;
    }

    @media screen and (max-width : 767px) {
      padding : 8px 15px;
    }
  }

  // .header__phones
  &__phones {
    @media screen and (max-width : 1139px) {
      margin-left : auto;
    }


  }

  // .header__personal-block
  &__personal-block {
  }

  // .header__catalog-menu-nav
  &__catalog-menu-nav {
    display         : flex;
    align-items     : center;
    justify-content : space-between;
    width           : 100%;
  }

  // .header__search-popup
  &__search-popup {
    position : absolute;
    z-index  : 1;
  }

  // .header__sale-btn
  &__sale-btn {
  }

  // .header__sale-menu
  &__big-catalog-menu {
  }
}


.main-menu {
  display     : flex;
  align-items : center;
  // .main-menu__item
  &__item {
    margin : 0 20px 0 0;
  }

  // .main-menu__link
  &__link {
    text-decoration : none;
    color           : #000;

    &:active,
    &:focus,
    &:hover {
      text-decoration : underline;
    }
  }
}

.call-order {
  font-weight     : 600;
  text-decoration : underline;
  color           : #000;

  &:active,
  &:focus,
  &:hover {
    text-decoration : none;
  }
}

.logo {
  text-decoration : none;

  @media screen and (max-width : 1139px) {
    display     : flex;
    align-items : center;
  }

  @media screen and (max-width : 560px) {
    flex-direction : column;
  }

  // .logo__image
  &__image {
    @media screen and (max-width : 1139px) {
      max-width : 167px;
    }
  }

  // .logo__text
  &__text {
    font-family    : 'Lato', sans-serif;
    font-size      : 13px;
    letter-spacing : -0.01em;
    opacity        : 0.7;
    color          : #454545;

    @media screen and (max-width : 1139px) {
      line-height : 13px;
      display     : block;
      max-width   : 126px;
      margin-left : 10px;
    }

    @media screen and (max-width : 560px) {
      margin-top    : 5px;
      margin-bottom : 10px;
      white-space   : nowrap;
    }

  }
}

.search-block {


  // .search-block__form
  &__form {
  }

  // .search-block__field
  &__field {
    font-size        : 14px;
    font-weight      : 600;
    width            : 100%;
    padding          : 14px 25px 15px;
    color            : #9d9d9d;
    border-radius    : 30px;
    background-color : #f8f8f8;

    &:focus {
      box-shadow : 0 0 0 2px #ccc;
    }

  }

  // .search-block__btn
  &__btn {
    position : absolute;
    top      : 12px;
    right    : 14px;

    @media screen and (max-width : 1139px) {
      top        : 50%;
      right      : 59px;
      margin-top : -12px;
    }

  }

  // .search-block__image
  &__image {
  }

  ::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
    opacity : 1; /* Firefox */
    color   : #9d9d9d;
  }

  :-ms-input-placeholder { /* Internet Explorer 10-11 */
    color : #9d9d9d;
  }

  ::-ms-input-placeholder { /* Microsoft Edge */
    color : #9d9d9d;
  }
}

.phones-block {

  @media screen and (max-width : 660px) {
    display : none;
  }

  // .phones-block__item
  &__item {
    font-size       : 18px;
    font-weight     : bold;
    line-height     : 22px;
    display         : block;
    text-decoration : none;
    color           : #161616;

    > span {
      color : #00cc52;
    }
  }
}

.personal-block {
  display     : flex;
  align-items : flex-end;

  @media screen and (max-width : 1139px) {
    margin-left : 30px;
  }
  @media screen and (max-width : 767px) {
    margin-left : auto;

  }

  @media screen and (max-width : 560px) {
    margin-top : 10px;
  }

  // .personal-block__item
  &__item {
  }
}

.item-personal-block {
  position        : relative;
  display         : block;
  margin-left     : 15px;
  text-align      : center;
  text-decoration : none;

  @media screen and (max-width : 768px) {
    margin-left : 5px;
  }


  // .item-personal-block__count
  &__count {
    font-size        : 16px;
    font-weight      : 600;
    position         : absolute;
    top              : -5px;
    left             : 50%;
    display          : flex;
    align-items      : center;
    justify-content  : center;
    width            : 23px;
    height           : 23px;
    color            : #fff;
    border-radius    : 50%;
    background-color : #feb618;

    @media screen and (max-width : 768px) {
      font-size   : 11px;
      line-height : 1.18181;
      top         : 0;
      right       : 0;
      left        : auto;
      width       : 16px;
      height      : 16px;
    }


  }

  // .item-personal-block__image
  &__image {
    position        : relative;
    display         : flex;
    align-items     : center;
    justify-content : center;
    width           : 36px;
    height          : 32px;
    margin          : 0 auto;


    > svg {
      @media screen and (max-width : 768px) {
        max-width : 22px;
      }
    }
  }

  // .item-personal-block__title
  &__title {
    font-size      : 12px;
    font-weight    : bold;
    display        : block;
    padding-top    : 1px;
    text-align     : center;
    letter-spacing : -0.015em;
    color          : #161616;

    @media screen and (max-width : 1139px) {
      display : none;
    }

  }
}

.catalog-menu {
  display     : flex;
  align-items : center;
  // .catalog-menu__item
  &__item {
    margin : 0 15px 0 0;


    &:last-child {
      margin-right : 0;
    }
  }

  // .catalog-menu__link
  &__link {
    font-size       : 14px;
    font-weight     : bold;
    line-height     : 1.2142;
    transition      : color 0.3s;
    text-decoration : none;
    color           : #fff;

    // .catalog-menu__link--active
    &--active,
    &:active,
    &:focus,
    &:hover {
      color : #feb618;
    }

    // .catalog-menu__link--sale
    &--sale {
      font-size      : 14px;
      font-weight    : 900;
      line-height    : 1.2142;
      position       : relative;
      display        : inline-block;
      text-transform : uppercase;
      color          : #fff;
    }
  }


}


.menu-sale-btn {
  height              : 20px;
  padding-right       : 40px;
  background-image    : url(../../img/svg/burger.svg);
  background-repeat   : no-repeat;
  background-position : right center;
  background-size     : 20px 14px;

  // .menu-sale-btn--show
  &--show {
    background-image : url(../../img/svg/burger-open.svg);
    background-size  : 20px 20px;
  }


}

.header__big-catalog-menu-wrapper {
  position         : absolute;
  z-index          : 10;
  top              : 100%;

  display          : none;
  width            : 100vw;
  margin-left      : calc((100vw - #{$contentWidth} + 30px) / -2);
  background-color : #00bc4b;

  &.show {
    display : block;
  }
}

.header__big-catalog-menu {
  max-width    : $contentWidth;
  margin       : 0 auto;
  padding      : 50px 0;
  column-count : 4;
  column-gap   : 60px;


}

.big-catalog-menu {


  // .big-catalog-menu--shown
  &--show {
    display : block;
  }

  // .big-catalog-menu__item
  &__item {
    display : inline-block;
  }

  // .big-catalog-menu__link
  &__link {
    font-size       : 16px;
    font-weight     : 800;
    line-height     : 1.25;
    display         : flex;
    text-decoration : none;
    text-transform  : uppercase;
    color           : #fff;


    > svg {
      margin-right : 10px;
    }

    &:active,
    &:focus,
    &:hover {
      color : #feb618;
    }
  }

  // .big-catalog-menu__icon
  &__icon {
    fill : #fff;
  }

  // .big-catalog-menu__sub
  &__sub {
    margin-top : 15px;
  }
}

.big-catalog-sub-menu {
  padding-left : 30px;

  // .big-catalog-sub-menu__item
  &__item {
    margin : 0 0 10px 0;
  }

  // .big-catalog-sub-menu__link
  &__link {

    font-size       : 14px;
    font-weight     : 500;
    line-height     : 1.2142;
    text-decoration : none;
    color           : #fff;

    &:active,
    &:focus,
    &:hover {
      color : #feb618;
    }
  }
}


.burger-mobile-menu {
  margin-left : 15px;


  @media screen and (min-width : 1140px) {
    display : none;
  }
  @media screen and (max-width : 560px) {
    margin-top : 13px;
  }
}


.has-menu-slide::after {
  content    : '';
  position   : fixed;
  z-index    : 10;
  top        : 0;
  right      : 0;
  bottom     : 0;
  left       : 0;
  display    : block;
  background : rgba(14, 14, 14, 0.8);
}

.mobile-menu-block {

}


////////////////////////////////

.mobile-menu-block {
  position         : fixed;
  z-index          : 50;
  top              : 0;
  right            : 0;
  bottom           : 0;
  overflow         : auto;
  padding          : 35px 25px 40px;
  transition       : transform 0.2s;
  transform        : translateX(100%);
  color            : #fff;
  background-color : #00cc52;


  // .mobile-menu-block--show
  &--show {
    transform : translateX(0);
  }

  // .mobile-menu-block--show
  &--show {
  }

  // .mobile-menu-block__close
  &__close {
    position : sticky;
    z-index  : 10;
    top      : 0;
    left     : 100%;
  }

  // .mobile-menu-block__inner
  &__inner {
    margin-top : -20px;

  }

  // .mobile-menu-block__title
  &__title {
    font-size   : 30px;
    font-weight : 800;
    line-height : 1.2333;
    color       : #fff;
  }

  // .mobile-menu-block__catalog-menu
  &__catalog-menu {
    margin-top : 25px;
  }

  // .mobile-menu-block__sale
  &__sale {
    font-size       : 14px;
    font-weight     : 900;
    line-height     : 1.2142;
    margin          : 25px 0;
    text-decoration : none;
    text-transform  : uppercase;
    color           : #feb618;
  }

  // .mobile-menu-block__main-menu
  &__main-menu {
    margin-top : 25px;
  }

  // .mobile-menu-block__call-forms
  &__call-forms {
    font-size   : 14px;
    font-weight : 600;
    line-height : 1.2142;
    display     : flex;
    margin-top  : 25px;
    color       : #fff;

    > svg {
      margin-top   : 2px;
      margin-right : 5px;
    }
  }

  // .mobile-menu-block__buttons
  &__buttons {

  }

  // .mobile-menu-block__call-order
  &__call-order {
    text-decoration : underline;
    color           : #fff;
  }

  // .mobile-menu-block__phones
  &__phones {
  }

  // .mobile-menu-block__phone
  &__phone {
    font-size       : 18px;
    font-weight     : bold;
    line-height     : 22px;
    display         : block;
    margin-top      : 25px;
    text-decoration : none;
    color           : #fff;

    > span {
      color : #feb618;
    }

  }
}

.mobile-catalog-menu {
  // .mobile-catalog-menu__item
  &__item {
    position : relative;
    margin   : 20px 0;
  }

  // .mobile-catalog-menu__link
  &__link {

    font-size       : 16px;
    font-weight     : 800;
    line-height     : 1.25;
    display         : flex;
    text-decoration : none;
    text-transform  : uppercase;
    color           : #fff;


    > svg {
      margin-right : 10px;
      fill         : #fff;
    }


    // .mobile-catalog-menu__link--active
    &--active,
    &:active,
    &:focus,
    &:hover {
      color : #feb618;
    }

  }

  // .mobile-catalog-menu__btn
  &__btn {
    position         : absolute;
    top              : 5px;
    right            : 0;
    transition       : all 0.1s;
    transform        : rotate(0);
    transform-origin : center;

    &.open {
      transform : rotate(-180deg);
    }
  }

  // .mobile-catalog-menu__icon
  &__icon {
  }

  // .mobile-catalog-menu__sub
  &__sub {
  }
}


.mobile-catalog-sub-menu {
  display      : none;
  padding-left : 30px;

  // .mobile-catalog-sub-menu__item
  &__item {
  }

  // .mobile-catalog-sub-menu__link
  &__link {
    font-size       : 14px;
    font-weight     : 500;
    line-height     : 1.2142;
    text-decoration : none;
    color           : #fff;

    // .mobile-catalog-sub-menu__link--active
    &--active,
    &:active,
    &:focus,
    &:hover {
      color : #feb618;
    }
  }
}

.mobile-catalog-menu__btn.open + .mobile-catalog-sub-menu {
  display : block;
}

.mobile-main-menu {
  // .mobile-main-menu__item
  &__item {
    margin : 15px 0;
  }

  // .mobile-main-menu__link
  &__link {
    font-size       : 14px;
    line-height     : 1.2142;
    text-decoration : none;
    color           : #fff;

    // .mobile-main-menu__link--active
    &--active,
    &:active,
    &:focus,
    &:hover {
      color : #feb618;
    }

  }
}


.search-result {
  z-index          : 50;
  padding          : 0 15px;
  border-radius    : 0 0 10px 10px;
  background-color : #f8f8f8;
}

.item-search-result {
  display       : flex;
  padding       : 15px 0;
  color         : #161616;
  border-bottom : 1px solid #f1f1f1;

  &:last-child {
    border-bottom : none;
  }

  // .item-search-result__content
  &__content {

  }

  // .item-search-result__image
  &__image {
    display          : flex;
    overflow         : hidden;
    align-items      : center;
    justify-content  : center;
    width            : 60px;
    height           : 60px;
    margin-right     : 10px;
    border-radius    : 50%;
    background-color : #fff;
  }

  // .item-search-result__name
  &__name {
    font-size       : 13px;
    text-decoration : none;
    color           : #161616;

  }

  // .item-search-result___price
  &___price {
    font-size   : 14px;
    font-weight : 500;
    line-height : 1.2142;

  }
}

.title-search-result {
  z-index : 1;
  padding : 0 20px;
}
