.product {
  // .product__order-delivery
  &__order-delivery {
    margin-top : 80px;
  }

  // .product__tabs
  &__tabs {
  }

  // .product__buy-with
  &__buy-with {
    margin-top : 80px;
  }

  // .product__viewed
  &__viewed {
    margin-top : 80px;
  }

  // .product__inner
  &__inner {
    display : flex;

    @media screen and (max-width : 660px) {
      display : block;
    }
  }

  // .product__images
  &__images {
    flex      : 0 0 calc(100% - 570px);
    max-width : calc(100% - 570px);

    @media screen and (max-width : 1139px) {
      flex      : 0 0 calc(100% - 360px);
      max-width : calc(100% - 360px);
    }

    @media screen and (max-width : 660px) {
      flex      : 1 1 auto;
      max-width : none;
    }
  }

  // .product__info
  &__info {
    flex        : 0 0 540px;
    margin-left : 30px;

    @media screen and (max-width : 1139px) {
      flex : 0 0 330px;
    }

    @media screen and (max-width : 660px) {
      flex        : 1 1 auto;
      margin-top  : 30px;
      margin-left : 0;
    }
  }

  // .product__footer
  &__footer {
    margin-top : 60px;
  }

  // .product__chip-exp
  &__chip-exp {
    margin-top : 80px;
  }

  // .product__questions-form
  &__questions-form {
    margin-top : 80px;
  }

  // .product__kit
  &__kit {
    margin-top : 80px;
  }
}

.order-delivery-product {
  // .order-delivery-product__title
  &__title {

    font-size   : 30px;
    font-weight : 800;
    line-height : 1.2333;
    text-align  : center;

    color       : #161616;
  }

  // .order-delivery-product__inner
  &__inner {
    display   : flex;
    flex-wrap : wrap;
    margin    : 50px -10px 0;
    padding   : 0 15px 0;
  }

  // .order-delivery-product__item
  &__item {
    flex   : 1 1 calc(100% / 4 - 20px);
    margin : 15px 10px 28px;

    @media screen and (max-width : 1024px) {
      flex : 1 1 calc(100% / 2 - 20px);
    }

    @media screen and (max-width : 560px) {
      flex : 1 1 auto;
    }
  }
}

.item-order-delivery {
  // .item-order-delivery__image
  &__image {
    display         : flex;
    align-items     : center;
    justify-content : center;
    height          : 170px;
  }

  // .item-order-delivery__title
  &__title {

    font-size   : 16px;
    font-weight : 600;
    line-height : 1.6;
    margin-top  : 20px;
    text-align  : center;
    color       : #161616;
