.props-product {
  font-size   : 14px;
  line-height : 1.2142;
  display     : flex;
  flex-wrap   : wrap;
  margin      : 30px -15px 0;

  @media screen and (max-width : 1139px) {
    display : block;
    margin  : 30px 0 0;
  }

  @media screen and (max-width : 660px) {
    margin-top : 20px;
  }


  // .props-product__item
  &__item {
    position : relative;
    flex     : 0 0 calc(100% / 2 - 30px);
    margin   : 0 15px 20px;
    cursor   : pointer;

    @media screen and (max-width : 1139px) {
      margin : 0 0 20px;
    }

  }

  // .props-product__title
  &__title {
    font-weight : 500;
  }

  // .props-product__field
  &__field {
    position         : relative;
    display          : flex;
    flex-wrap        : wrap;
    height           : 40px;
    margin-top       : 10px;
    padding          : 6px 30px 6px 10px;
    border-radius    : 10px;
    background-color : #f8f8f8;

    // .props-product__field--open
    &--open {
      border-radius : 10px 10px 0 0;
      box-shadow    : 1px 4px 3px -2px black;
    }

    &::after {
      content          : '';
      position         : absolute;
      top              : 18px;
      right            : 10px;
      display          : block;
      width            : 14px;
      height           : 8px;
      background-image : url(../../img/svg/props-product-arrow.svg);

    }
  }

  // .props-product__icon
  &__icon {
    overflow      : hidden;
    align-self    : center;
    width         : 28px;
    height        : 28px;
    margin-right  : 10px;
    border-radius : 50%;


    > img {
      display    : inline-block;
      max-width  : 100%;
      max-height : 100%;
    }
  }

  // .props-product__name
  &__name {
    font-size     : 13px;
    line-height   : 1.2307;
    overflow      : hidden;
    flex          : 0 0 calc(100% - 38px);
    white-space   : nowrap;
    text-overflow : ellipsis;
  }

  // .props-product__price
  &__price {
    font-size   : 11px;
    font-weight : 500;
    position    : relative;
    top         : -13px;
    margin-left : 38px;
  }

  // .props-product__list
  &__list {
    width : 100%;
  }
}

.props-product__field--open + .list-props-product {
  z-index : 1;
  display : block;
}

.list-props-product {
  position         : absolute;
  top              : calc(100% + -1px);
  display          : none;
  overflow         : auto;
  max-height       : 300px;
  margin-top       : 0;
  padding          : 6px 10px 6px 10px;
  border-top       : 1px solid #d8d8d8;
  border-radius    : 0 0 10px 10px;
  background-color : #f8f8f8;
  box-shadow       : 1px 1px 3px -2px black;


  // .list-props-product__item
  &__item {
    display   : flex;
    flex-wrap : wrap;
    margin    : 2px 0;
  }

  // .list-props-product__icon
  &__icon {
    overflow      : hidden;
    align-self    : center;
    width         : 28px;
    height        : 28px;
    margin-right  : 10px;
    border-radius : 50%;


    > img {
      display    : inline-block;
      max-width  : 100%;
      max-height : 100%;
    }
  }

  // .list-props-product__name
  &__name {
    font-size     : 13px;
    line-height   : 1.2307;
    overflow      : hidden;
    flex          : 1 1 calc(100% - 40px);
    white-space   : nowrap;
    text-overflow : ellipsis;
  }

  // .list-props-product__price
  &__price {
    font-size   : 11px;
    font-weight : 500;
    position    : relative;
    top         : -13px;
    margin-left : 38px;
  }

  // .list-props-product__hidden-image
  &__hidden-image {
    display         : flex;
    align-items     : center;
    justify-content : center;
    width           : 200px;
    height          : 200px;

    > img {
      max-width  : 100%;
      max-height : 100%;
    }
  }
}


