    font-size            : 14px;
    font-weight          : 600;
    line-height          : 1.2142;
    color                : #00cc52;
    text-decoration-line : underline;

    &:active,
    &:hover,
    &:focus {
      text-decoration : none;
    }
  }

  // .price-product__discount-title
  &__discount-title {
    font-size   : 14px;
    font-weight : 500;
    line-height : 1.2142;


    color       : #9d9d9d;
  }

  // .price-product__price
  &__price {

    font-size   : 40px;
    font-weight : 800;
    line-height : 1.225;
    color       : #161616;

    // .price-product__price--old
    &--old {
      font-size            : 26px;
      font-weight          : 500;
      line-height          : 1.2307;
      margin-top           : 10px;
      color                : #9d9d9d;
      text-decoration-line : line-through;
    }
  }
}


